import '../css/App.css'
import React, { Component } from 'react'

export default class Setting extends Component {

  state = {
    on: false,
  }

  switchON = () => {
    this.setState({
      on: !this.state.on
    })
  }

  render(){
    return(
      <div >
      {this.state.on && this.props.children}
      <settingbutton>
                <button className="morebutton"onClick={this.switchON}>
                  SETT
                  TING
                </button>
                </settingbutton>
      </div>
    )

  }

}
