import '../css/App.css'
import React, { Component } from 'react'

export default class Mapsbutton extends Component {

  state = {
    on: false,
  }

  switchON = () => {
    this.setState({
      on: !this.state.on
    })
  }

  render(){
    return(
      <div >
      {this.state.on && this.props.children}
  
                <mapsbutton>
                  <button className="morebutton"onClick={this.switchON}>
                    MAPS
                  </button>
                </mapsbutton>
      </div>
    )

  }

}
