import '../css/App.css'
import React, { Component } from 'react'

export default class Filter extends Component {

  state = {
    on: false,
  }

  switchON = () => {
    this.setState({
      on: !this.state.on
    })
  }

  render(){
    return(
      <div>
      {this.state.on && this.props.children}

      <filterbutton>
                <button className="morebutton"onClick={this.switchON}>
                  FIL
                  TER
                </button>
                </filterbutton>
      </div>
    )

  }

}
