import '../css/Event.css'
import React, { Component } from 'react'

export default class What2DoButton extends Component {

  state = {
    on: false,
  }

  switchON = () => {
    this.setState({
      on: !this.state.on
    })
  }

  render(){
    return(
      <div >
      {this.state.on && this.props.children}

                <button className="mainbutton" onClick={this.switchON}>
                  WHAT
                  2
                  DO
                </button>

      </div>
    )

  }

}
