import React, {Component} from 'react';
import '../css/Event.css';
import 'bootstrap/dist/css/bootstrap.css';

export default class TheEvent extends Component {


  constructor(props){
    super(props)


    this.state = {
      data: [],
      eventid: []
    }

    this.updateData = this.updateData.bind(this);
  }


  handleClick(){
    console.log('IM WORKING')
  }

  componentWillMount() {

  // Your parse code, but not seperated in a function
      var csvFilePath = require("../csv/gratisinBerlin_9_9.csv");
      var Papa = require("papaparse/papaparse.min.js");
      Papa.parse(csvFilePath, {
        header: true,
        download: true,
        skipEmptyLines: true,
        // Here this is also available. So we can call our custom class method
        complete: this.updateData
      });
    }

    updateData(result) {

      const data = result.data
      var filterKategorie = false;
      var getKategorie = localStorage.getItem('Kategorie');


         for (let index = 0; index < 60; index++) {

           filterKategorie = data[index].Kategorie === getKategorie;

           while(filterKategorie){

              this.setState({ data: this.state.data.concat(data[index]) })

              filterKategorie = false;
           }
         }



      // Here this is available and we can call this.setState (since it's binded in the constructor)
    //  this.setState({troll}); // or shorter ES syntax: this.setState({ data });
    }



    reloadPage(e){


      this.componentWillMount();
      localStorage.setItem('eventNr', e)


    }



  render(){

    const titel = this.state.data.map(data =><ti class='titel' >{data.Titel}</ti>)
    const bild =  this.state.data.map(data =><img alt={data.Bild} class='bild' src={data.Bild}></img>)
    const zeit = this.state.data.map(data =><dt class='zeit' >{data.Zeit}</dt>)
    const ort = this.state.data.map(data =><dt class='ort'>{data.Ort}</dt>)
    const kategorie = this.state.data.map(data =>  <dt class='kategorie'>{data.Kategorie}</dt>  )
    const beschreibung = this.state.data.map(data =>  <dt class='kategorie'>{data.Beschreibung}</dt>  )
    const preis = this.state.data.map(data =><dt class='preis'>{data.Preis}</dt>)

    var getEventNr = localStorage.getItem('eventNr');

    console.log(getEventNr)

  return (

    <div class='Event'>

      {titel[getEventNr]}<br />
      {bild[getEventNr]}
      {zeit[getEventNr]}
      {ort[getEventNr]}
      {kategorie[getEventNr]}
      {beschreibung[getEventNr]}<br /><br />
      {preis[getEventNr]}

        <br /><br /><br /><br /><br />

            <div class="paginationN">
                  <nav aria-label="...">
                  <ul class="pagination pagination-lg">
                    <li onClick={() => this.reloadPage(0)} class="page-item"><a class="page-link" href="#1">1</a></li>
                    <li onClick={() => this.reloadPage(1)} class="page-item"><a class="page-link">2</a></li>
                    <li onClick={() => this.reloadPage(2)} class="page-item"><a class="page-link" href="#3">3</a></li>
                    <li onClick={() => this.reloadPage(3)} class="page-item"><a class="page-link">4</a></li>
                    <li onClick={() => this.reloadPage(4)} class="page-item"><a class="page-link" href="#3">5</a></li>
                  </ul>
                </nav>
            </div>
      </div>
    );
  }
}
